import { Card, Row, Col } from 'antd';
import React, { useEffect } from 'react';
import TitleComponent from '../TitleComponent';
import '../../css/Blog.css'
import CardContainer from './CardContainer';
import axios from 'axios';
import cheerio from 'cheerio';
import { Waypoint } from 'react-waypoint';
import $ from 'jquery';

const Blog = props => {
    const rowGutter = [16, 16];
    const urlList = [
        'https://sungwon.blog/korean-ocr-step-by-step-1',
        'https://sungwon.blog/korean-ocr-step-by-step-1',
        'https://sungwon.blog/korean-ocr-step-by-step-1',
        'https://sungwon.blog/korean-ocr-step-by-step-1',
        'https://sungwon.blog/korean-ocr-step-by-step-1',
        'https://sungwon.blog/korean-ocr-step-by-step-1'
    ]

    return (
        <section id="blog">
            <div className="blog container flex">
                <TitleComponent title="blog" />
                <Waypoint bottomOffset="350px" onEnter={e => {
                    urlList.map((url,ind) => {
                        var elem = $('#blog-waypoint-' + ind),
                            animationClass = elem.attr('trigger'),
                            animationDelay = elem.attr('trigger-delay');
                        if (animationDelay) {
                            elem.css({
                                '-webkit-animation-delay': animationDelay,
                                '-moz-animation-delay': animationDelay,
                                'animation-delay': animationDelay
                            });
                        }
                        elem.addClass('trigger').addClass('animated');
                        elem.addClass('trigger').addClass(animationClass);
                    })
                }} />
                <Row className="blog-row-container" gutter={rowGutter}>
                    {
                        urlList.map((url, ind) => {
                            return (
                                <CardContainer span={8} url={url} id={"blog-waypoint-" + ind} delay={`.${ind}s`} key={'blog-card-'+ind}>

                                </CardContainer>
                            )
                        })
                    }
                </Row>
            </div>
            <div className="btn-load-more">
                Load more
            </div>
        </section>
    )
}

export default Blog;