import './App.css';
import './css/Animation.css'
import TabsComponent from './Components/TabsComponent';
import MainComponent from './Components/MainComponent';
import Home from './Components/Home/Home';
import 'antd/dist/antd.css';
import { useEffect } from 'react';
import $ from 'jquery';

const test = new Array(100);
test.fill(1);

function getDocHeight() {
  var doc = document;
  return Math.max(
    doc.body.scrollHeight, doc.documentElement.scrollHeight,
    doc.body.offsetHeight, doc.documentElement.offsetHeight,
    doc.body.clientHeight, doc.documentElement.clientHeight
  );

}

function App() {
  function highlightLink(anchor) {
    document.getElementsByClassName('')
    $('nav .active').removeClass('active');
    $('nav')
      .find('[dest="' + anchor + '"]')
      .addClass('active');
  }

  function onScrollInit(items, elemTrigger) {
    var offset = $(window).height() / 1.6;
    items.each(function() {
      var elem = $(this),
        animationClass = elem.attr('trigger'),
        animationDelay = elem.attr('trigger-delay');

      elem.css({
        '-webkit-animation-delay': animationDelay,
        '-moz-animation-delay': animationDelay,
        'animation-delay': animationDelay
      });

      var trigger = elemTrigger ? trigger : elem;
      console.log(trigger.waypoint);
      // trigger.waypoint(
      //   function() {
      //     elem.addClass('trigger').addClass(animationClass);
      //   },
      //   {
      //     triggerOnce: true,
      //     offset: offset
      //   }
      // );
    });
  }

  useEffect(() => {
    $(window).on('scroll', () => {
      var pos = $(window).scrollTop();
      var pos2 = pos + 50;
      // Link Highlighting
      if (pos2 > $('#home').offset().top) {
        highlightLink('home');
      }
      if (pos2 > $('#about').offset().top) {
        highlightLink('about');
      }
      if (pos2 > $('#works').offset().top) {
        highlightLink('works');
      }
      if (pos2 > $('#blog').offset().top) {
        highlightLink('blog');
      }
      if (
        pos2 > $('#contact').offset().top ||
        pos + $(window).height() === $(document).height()
      ) {
        highlightLink('contact');
      }
    })
    // setTimeout(function() {
    //   onScrollInit($('.trigger'));
    // }, 10);
  }, [])
  return (
    <>
      <Home />
      <TabsComponent />
      <MainComponent />
      {/* {test.map(e => {
        return <div>test</div>
      })} */}
    </>
  );
}

export default App;
